@import url('https://fonts.googleapis.com/css2?family=Crimson+Text:ital,wght@0,400;0,600;1,400&family=Great+Vibes&family=Playfair+Display:wght@400;700&display=swap');

:root {
  --bg-dark: #0F0202;
  --surface: rgba(255, 255, 255, 0.05);
  --surface-highlight: rgba(212, 175, 55, 0.1);
  --gold: #D4AF37;
  --red: #E63946;
  --red-deep: #B91C1C;
  --cream: #FFF8E7;
  --cream-muted: rgba(255, 248, 231, 0.8);
  --text-muted: rgba(255, 248, 231, 0.6);
  --border-dark: rgba(255, 255, 255, 0.1);
  --font-heading: 'Playfair Display', serif;
  --font-accent: 'Great Vibes', cursive;
  --font-body: 'Crimson Text', serif;
}
/* ============================================
   FOR MY AMOR - QASEEDA NOOR
   Valentine's Day Website Styles
   ============================================ */

/* === FLOATING HEARTS === */
@keyframes float-up {
  0% {
    transform: translateY(100vh) rotate(0deg) scale(1);
    opacity: 0;
  }
  10% {
    opacity: 0.8;
  }
  90% {
    opacity: 0.6;
  }
  100% {
    transform: translateY(-100px) rotate(720deg) scale(0.3);
    opacity: 0;
  }
}

@keyframes sway {
  0%, 100% { transform: translateX(0px); }
  25% { transform: translateX(15px); }
  75% { transform: translateX(-15px); }
}

.floating-heart {
  position: fixed;
  z-index: 1;
  pointer-events: none;
  animation: float-up linear infinite;
}

.floating-heart svg {
  animation: sway 3s ease-in-out infinite;
  filter: drop-shadow(0 0 6px rgba(230, 57, 70, 0.5));
}

/* === TYPEWRITER CURSOR === */
@keyframes blink-cursor {
  0%, 50% { opacity: 1; }
  51%, 100% { opacity: 0; }
}

.typewriter-cursor {
  display: inline-block;
  width: 3px;
  height: 1em;
  background: var(--gold);
  margin-left: 4px;
  animation: blink-cursor 0.8s infinite;
  vertical-align: text-bottom;
}

/* === GOLDEN GLOW === */
@keyframes golden-glow {
  0%, 100% {
    text-shadow: 0 0 20px rgba(212, 175, 55, 0.3), 0 0 40px rgba(212, 175, 55, 0.1);
  }
  50% {
    text-shadow: 0 0 30px rgba(212, 175, 55, 0.5), 0 0 60px rgba(212, 175, 55, 0.2), 0 0 80px rgba(212, 175, 55, 0.1);
  }
}

.golden-glow {
  animation: golden-glow 3s ease-in-out infinite;
}

/* === SHIMMER TEXT === */
@keyframes shimmer {
  0% { background-position: -200% center; }
  100% { background-position: 200% center; }
}

.shimmer-text {
  background: linear-gradient(90deg, var(--gold) 0%, var(--cream) 25%, var(--gold) 50%, var(--cream) 75%, var(--gold) 100%);
  background-size: 200% auto;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: shimmer 4s linear infinite;
}

/* === SCROLL INDICATOR === */
@keyframes bounce-down {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0) translateX(-50%); }
  40% { transform: translateY(12px) translateX(-50%); }
  60% { transform: translateY(6px) translateX(-50%); }
}

.scroll-indicator {
  animation: bounce-down 2s ease infinite;
}

/* === ENVELOPE === */
.envelope-wrapper {
  perspective: 800px;
  cursor: pointer;
}

.envelope {
  position: relative;
  width: 320px;
  height: 220px;
  margin: 0 auto;
  transition: transform 0.3s ease;
}

.envelope:hover {
  transform: scale(1.02);
}

.envelope-body {
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #B91C1C, #7F1D1D);
  border-radius: 0 0 12px 12px;
  box-shadow: 0 8px 30px rgba(185, 28, 28, 0.3);
  overflow: hidden;
}

.envelope-body::after {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, transparent 40%, rgba(212, 175, 55, 0.1) 50%, transparent 60%);
}

.envelope-flap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background: linear-gradient(180deg, #991B1B, #B91C1C);
  clip-path: polygon(0 0, 50% 100%, 100% 0);
  transform-origin: top center;
  transition: transform 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 3;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

.envelope-seal {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 50px;
  background: radial-gradient(circle, var(--gold), #B8860B);
  border-radius: 50%;
  z-index: 4;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 10px rgba(212, 175, 55, 0.5);
  transition: opacity 0.4s ease, transform 0.4s ease;
}

.envelope.open .envelope-flap {
  transform: rotateX(180deg);
}

.envelope.open .envelope-seal {
  opacity: 0;
  transform: translate(-50%, -50%) scale(0.5);
}

.letter-paper {
  position: absolute;
  top: 20%;
  left: 10%;
  width: 80%;
  height: 70%;
  background: linear-gradient(180deg, #FFF8E7, #F5E6C8);
  border-radius: 4px;
  z-index: 2;
  transition: transform 1s cubic-bezier(0.4, 0, 0.2, 1) 0.3s;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
}

.envelope.open .letter-paper {
  transform: translateY(-140%);
}

.letter-paper span {
  font-family: var(--font-accent);
  color: var(--red-deep);
  font-size: 1.2rem;
}

/* === LETTER CONTENT === */
.letter-content {
  background: linear-gradient(180deg, #FFF8E7, #F5E6C8);
  color: #2D1B0E;
  font-family: var(--font-body);
  line-height: 1.9;
  border-radius: 12px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3), inset 0 1px 0 rgba(255, 255, 255, 0.5);
  position: relative;
  overflow: hidden;
}

.letter-content::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, var(--gold), var(--red), var(--gold));
}

/* === PHOTO GALLERY === */
.photo-card {
  overflow: hidden;
  border-radius: 16px;
  border: 2px solid var(--border-dark);
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}

.photo-card:hover {
  border-color: var(--gold);
  transform: translateY(-8px);
  box-shadow: 0 20px 50px rgba(212, 175, 55, 0.15), 0 0 30px rgba(230, 57, 70, 0.1);
}

.photo-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.7s cubic-bezier(0.4, 0, 0.2, 1);
}

.photo-card:hover img {
  transform: scale(1.08);
}

.photo-card .photo-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(to top, rgba(15, 2, 2, 0.9) 0%, transparent 50%);
  opacity: 0;
  transition: opacity 0.5s ease;
  display: flex;
  align-items: flex-end;
  padding: 1.5rem;
}

.photo-card:hover .photo-overlay {
  opacity: 1;
}

/* === TIMELINE === */
.timeline-line {
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 2px;
  background: linear-gradient(to bottom, transparent, var(--gold), var(--red), var(--gold), transparent);
  transform: translateX(-50%);
}

.timeline-dot {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: var(--gold);
  border: 3px solid var(--bg-dark);
  box-shadow: 0 0 15px rgba(212, 175, 55, 0.5);
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
}

/* === HEART PULSE === */
@keyframes heart-pulse {
  0%, 100% { transform: scale(1); }
  25% { transform: scale(1.15); }
  50% { transform: scale(1); }
  75% { transform: scale(1.1); }
}

.heart-pulse {
  animation: heart-pulse 1.5s ease-in-out infinite;
}

/* === MUSIC PLAYER === */
@keyframes spin-slow {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.music-btn.playing {
  animation: spin-slow 3s linear infinite;
}

.music-btn {
  box-shadow: 0 4px 20px rgba(212, 175, 55, 0.3);
  transition: box-shadow 0.3s ease, transform 0.3s ease;
}

.music-btn:hover {
  box-shadow: 0 4px 30px rgba(212, 175, 55, 0.5);
  transform: scale(1.1);
}

/* === REASON CARDS === */
.reason-card {
  background: var(--surface);
  border: 1px solid var(--border-dark);
  border-radius: 16px;
  padding: 1.5rem;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.reason-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, transparent, var(--gold), transparent);
  opacity: 0;
  transition: opacity 0.4s ease;
}

.reason-card:hover {
  border-color: var(--gold);
  transform: translateY(-4px);
  box-shadow: 0 10px 30px rgba(212, 175, 55, 0.1);
}

.reason-card:hover::before {
  opacity: 1;
}

/* === SECTION DIVIDER === */
.section-divider {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 2rem 0;
}

.section-divider .line {
  width: 60px;
  height: 1px;
  background: linear-gradient(90deg, transparent, var(--gold), transparent);
}

/* === MOBILE RESPONSIVE === */
@media (max-width: 768px) {
  .envelope {
    width: 260px;
    height: 180px;
  }

  .timeline-line {
    left: 20px;
  }

  .timeline-dot {
    left: 20px;
  }
}

/* === HEART BURST === */
@keyframes heart-burst {
  0% { transform: scale(0) rotate(0deg); opacity: 1; }
  50% { transform: scale(1.5) rotate(180deg); opacity: 0.8; }
  100% { transform: scale(0) rotate(360deg); opacity: 0; }
}

.heart-burst {
  animation: heart-burst 0.6s ease-out forwards;
}

/* === SURPRISE POP === */
@keyframes pop-in {
  0% { transform: scale(0); opacity: 0; }
  70% { transform: scale(1.2); }
  100% { transform: scale(1); opacity: 1; }
}

.pop-in {
  animation: pop-in 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

